:root {
  --swipe-to-delete-bg-color: #ff3b30; }

.SwipeToDelete {
  --right-side-content-width: 0;
  transform: translateX(var(--translateX));
  transition: transform var(--animation-speed) ease-out; }
  .SwipeToDelete-RightSideContentWrapper {
    background-color: var(--swipe-to-delete-bg-color);
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: calc(-1 * var(--translateX));
    transition: width var(--animation-speed) ease-out;
    overflow: hidden; }
  .SwipeToDelete-RightSideContent {
    height: 100%;
    margin-left: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    width: var(--right-side-content-width); }
