:root {
  --whislist-swipe-to-delete-color: #fff;
  --whislist-qty-button-size: 20px; }

@media (max-width: 810px) {
  .WishlistItem {
    padding: 1.4rem var(--my-account-wrapper-padding-mobile);
    border-top: 1px solid var(--wishlist-item-border-color); } }

@media (max-width: 810px) {
  .WishlistItem:first-of-type {
    border-top: none;
    padding-top: 0; } }

@media (max-width: 810px) {
  .WishlistItem:last-of-type {
    margin-bottom: 0; } }

@media (min-width: 811px) {
  .WishlistItem-ProductCard {
    padding: 1.2rem 0;
    margin-bottom: 0; } }

@media (max-width: 810px) {
  .WishlistItem-ContentWrapper {
    display: flex;
    flex: 1;
    min-height: 130px; } }

.WishlistItem-Remove {
  position: absolute;
  right: 5px;
  top: 5px;
  height: 30px;
  width: 30px; }
  .WishlistItem-Remove::before, .WishlistItem-Remove::after {
    content: '';
    position: absolute;
    width: 1px;
    height: 20px;
    transform-origin: 50% 50%;
    top: 7px;
    transform: rotate(-45deg);
    background-color: var(--header-color); }
  .WishlistItem-Remove::after {
    transform: rotate(45deg); }
  .WishlistItem-Remove::before, .WishlistItem-Remove::after {
    top: 5px; }

.WishlistItem-AddToCart {
  display: inline-block;
  margin-top: 0; }
  @media (max-width: 810px) {
    .WishlistItem-AddToCart {
      padding: 7px;
      font-size: 1.4rem;
      margin-left: auto; } }
  @media (min-width: 811px) {
    .WishlistItem-AddToCart {
      margin-top: 10px; } }
  .WishlistItem-AddToCart_isEditingActive {
    display: none; }
  .WishlistItem-AddToCart.Button {
    width: auto; }
    @media (min-width: 811px) {
      .WishlistItem-AddToCart.Button {
        width: 100%; } }

.WishlistItem-QuantityWrapper {
  display: flex;
  align-items: center; }

.WishlistItem-QuantityText {
  margin-right: 10px; }

.WishlistItem-Price {
  display: inline-block; }
  @media (min-width: 811px) {
    .WishlistItem-Price {
      float: right;
      text-align: right; } }

@media (max-width: 810px) {
  .WishlistItem-Content {
    flex: 1;
    display: flex;
    flex-direction: column; } }

.WishlistItem-ImageWrapper {
  margin-right: 15px;
  width: 100px; }

.WishlistItem-CommentField input {
  width: 100%; }

.WishlistItem-CommentField_isNotEditingActive input {
  padding: 0;
  border: none; }

.WishlistItem-RowWrapper {
  margin-top: auto;
  display: flex;
  align-items: center;
  justify-content: space-between; }
  @media (max-width: 810px) {
    .WishlistItem-RowWrapper {
      padding-top: 10px; } }
  @media (min-width: 811px) {
    .WishlistItem-RowWrapper {
      margin-top: 10px; } }

.WishlistItem-AttributeWrapper {
  margin: 5px 0 12px; }

.WishlistItem-SelectWrapper {
  display: flex; }

.WishlistItem-Select {
  display: none;
  align-items: center;
  justify-content: center; }
  .WishlistItem-Select_isEditingActive {
    width: calc(50px - 1.4rem);
    padding-right: 1.4rem;
    display: flex; }

@media (max-width: 810px) {
  .WishlistItem-Picture {
    height: 100%; } }

.WishlistItem-Picture img {
  object-fit: cover; }

.WishlistItem-SwipeToDeleteRightSide {
  height: 100%;
  font-weight: 600;
  color: var(--whislist-swipe-to-delete-color); }

.WishlistItem .ProductCard-Name {
  white-space: inherit; }

.ProductAttribute {
  font-size: 1rem;
  text-transform: uppercase;
  border: 1px solid #adadad;
  color: #adadad;
  padding: 3px 5px;
  margin-right: 5px;
  display: inline-block; }
